@media (max-width: 1200px) {
}
@media (max-width: 992px) {
  .main-header {
    .navbar {
      .navbar-toggler,
      .logo,
      .navbar-brand {
        z-index: 2;
      }
      .navbar-collapse {
        z-index: 1;
        margin-top: -10px;
        background: #f5fcfd;
      }
    }

    &__label {
      display: none;
    }
    &__phone {
      position: static;
      padding: 0 0.5em;
    }
  }
  .navbar-nav {
    .nav-item {
      &:not(:last-child) {
        &::after {
          content: "";
        }
      }

      &:not(:first-child) {
        padding-left: 0.5rem;
      }
    }
  }
  .about-section {
    &__gears-border {
      &-header {
        top: -18px;
      }
    }
  }
  .page-title {
    &-wrapper {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }
}
@media (max-width: 768px) {
  .about-section {
    margin-top: -105px;
    &__gears-border {
      background-color: #d8d1c7;
      background-image: none;
      border: 3px solid #b99c81;

      position: relative;
      &-header {
        width: 274px;
        top: -41px;
      }
      &-inner {
        padding: 35px 15px;
      }
    }
  }

  .pagination {
    &__prev,
    &__next {
      width: 50%;
    }
  }
}
@media (max-width: 576px) {
  .logo {
    width: 160px;
  }
}
